<template>
  <div>
    <b-breadcrumb :items="menus"/>
    <b-container fluid>
      <b-table bordered responsive hover :items="bloodTubeLists" :fields="fields" head-variant='light' show-empty :empty-text="tableEmptyText">
        <template slot="blood_tube_batch" slot-scope="row">
          <b-link @click="getDesc(row.item)">{{row.item._showDetails?'收起':'展开'}}</b-link>
        </template>
        <!-- 详情模板 -->
        <template slot="row-details" slot-scope="row">
          <b-card>
            <div v-if="row.item.blood_tube_batch==='[]'||!row.item.blood_tube_batch" style="color:#ccc">暂无批次</div>
            <ul class="versionUL" v-if="row.item.blood_tube_batch!=='[]'&&row.item.blood_tube_batch">
              <li v-for="(item,index) in JSON.parse(row.item.blood_tube_batch)" :key="index">{{item}}</li>
            </ul>
          </b-card>
        </template>
        <template slot="operate" slot-scope="row">
          <b-button size="sm" class="mr-2" variant="outline-theme" @click="edit(row)">
           编辑
          </b-button>
          <b-modal :ref="'editBloodTubeModal'+row.index" hide-footer no-close-on-backdrop title="采血管 -- 编辑">
            <b-form class="smForm" style="text-align:left" @submit.prevent="onSubmit($event,row)">
              <b-form-group :id="'edit_blood_tube_nameGroup'+row.index" label="采血管名称:" label-size="sm" :label-for="'edit_blood_tube_name'+row.index">
                <b-form-input autocomplete="off" size="sm" type="text" :id="'edit_blood_tube_name'+row.index" v-model.trim="currentForm.blood_tube_name" required disabled>
                </b-form-input>
              </b-form-group>
            <b-form-group :id="'edit_blood_tube_batchGroup'+row.index" label="采血管批次:" label-size="sm" :label-for="'edit_blood_tube_batch'+row.index">
            <ul class="versionUL">
                <li v-for="(item,index) in currentForm.blood_tube_batch_arr" :key="index">
                  {{item}}
                  <img src="/images/errortips.png" alt="" title="点击删除" @click="deleteBloodTubeBatchItem(currentForm,index)">
                </li>
              </ul>
              <b-input-group size="sm">
                <b-form-input type="text" v-model.trim="currentForm.blood_tube_batch_input" placeholder="请输入要添加的批次"></b-form-input>
                <b-input-group-append>
                  <b-btn variant="outline-theme" @click="saveBloodTubeVesion(currentForm)">添加批次</b-btn>
                </b-input-group-append>
              </b-input-group>
            </b-form-group>
              <b-button type="submit" style="width:100%" variant="info">提交</b-button>
            </b-form>
          </b-modal>
        </template>
      </b-table>
      <div class="paginationCon">
        <b-pagination size="md" :number-of-pages="10" :total-rows="total" v-model="currentPage" @input="changePage" :per-page="10"></b-pagination>
      </div>
    </b-container>
  </div>
</template>
<script>
  export default{
    mounted(){
      
      this.getTableData()
      
    },
    data () {
      return {
        menus: [{
          text: '采血管管理',
          href: '#'
        }],
        currentForm:{
          blood_tube_name:'',
          blood_tube_batch_arr:[],
          blood_tube_batch_input:'',
        },
        fields: {
          blood_tube_name:{
            label:'采血管名称'
          },
          blood_tube_batch:{
            label:'采血管批次'
          },
          operate:{
            label:'操作',
            thStyle:{
              width:'150px'
            }
          }
        },
        tableEmptyText:'正在请求数据...',
        bloodTubeLists: [],
        currentPage:1,
        total:0
      }
    },
    methods:{
      getTableData(){
        this.$Loading.start()
        this.tableEmptyText = "正在请求数据..."
        this.$http.post("/api/bloodtube/list",qs.stringify({page:this.currentPage})).then(response=>{
          // console.log(response.data)
          this.bloodTubeLists=response.data.data
          this.bloodTubeLists.forEach(item=>{
            this.$set(item,'_showDetails',false)
          })
          Number(response.data.total)===0?this.tableEmptyText = "[空]":null
          this.total = Number(response.data.total)
          this.$Loading.finish()
        })
      },
      getDesc(item){
        if(!item._showDetails){
          item._showDetails=true
        }else{
          item._showDetails=false
        }
      },
      saveBloodTubeVesion(form){
        // 空值不能提交
        if(form.blood_tube_batch_input===''){
          this.$Message.error("请先输入采血管批次，再提交！")
          return false
        }
        // 控制该批次是否已存在
        if(form.blood_tube_batch_arr.filter(item=>{
          return item===form.blood_tube_batch_input
        }).length===0){
          form.blood_tube_batch_arr.push(form.blood_tube_batch_input)
          form.blood_tube_batch_input=''
        }else{
          this.$Message.error("该批次已存在！请勿重复添加")
        }
      },
      deleteBloodTubeBatchItem(form,index){
        // console.log(index);
        form.blood_tube_batch_arr.splice(index,1)
      },
      edit(row){
        if(this.currentForm.bloodtubeid!==row.item.bloodtubeid){
          for(var k in row.item){
            if(k==="blood_tube_batch"&&row.item.blood_tube_batch){
              this.currentForm.blood_tube_batch_arr=JSON.parse(row.item[k])
            }else{
              this.currentForm[k]=row.item[k]
            }
          }
        }
        this.$refs['editBloodTubeModal'+row.index].show()
      },
      onSubmit(evt,row){
        this.$Loading.start()
        evt.preventDefault();
        if(this.currentForm.blood_tube_batch_arr.length===0){
          this.$Message.warning("请添加采血管批次!");
          this.$Loading.error()
          return false;
        }
        this.currentForm.blood_tube_batch=JSON.stringify(this.currentForm.blood_tube_batch_arr)
        this.$http.post("/api/bloodtube/editBloodtube",qs.stringify(this.currentForm)).then(response=>{
          this.getTableData()
          this.currentForm={
            blood_tube_name:'',
            blood_tube_batch_arr:[],
            blood_tube_batch_input:'',
          }
          this.$refs['editBloodTubeModal'+row.index].hide()
          this.$Loading.finish()
        })
      },
      changePage(){
        this.getTableData()
      }
    }

  }
</script>
<style lang='less' scoped>
@import '../../../../less/init.less';
.versionUL{
  display:flex;
  flex-wrap:wrap;
  margin-bottom:5px;
  li{
    padding: 5px 10px;
    margin: 5px;
    background-color:#eee;
    border-radius:5px;
    position:relative;
    img{
      position:absolute;
      top:-6px;
      right:-6px;
    }
  }
}
.btn-sm{
  padding: .08rem .5rem !important;
}
.addCon{
  text-align: right;
  padding-right: 20px;
  margin-bottom:10px;
}
.paginationCon{
  overflow: hidden;
  .pagination{
    float: right;
  }
}

</style>


